.App {
  text-align: center;
  width: 95%;
  margin: 15px auto;
} 
.container-drag{
  text-align: center;
}
.wip{
  position: absolute;
  width: 150px;
  height: 100%;
  top:10;
  left: 0;
  background-color: gray;
  border-right: 1px dotted;
}
.header{
  display: inline-block;
  margin: 0;
  padding: 0;
  background-color: azure;
  width: 100%;
}
.task-header{
  display: inline-block;
  background-color: skyblue;
  width: 100%;
}
.droppable{
  position: absolute;
  width: 150px;
  height: 100vh;
  right:0;
  top:10;
  background-color: lightcoral;
  border-left: 1px dotted;
}
.draggable{
  width: 100px;
  height: 100px;
  background-color: yellow;
  margin: 5px auto;
  line-height: 5em;
}
